*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 15px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Times New Roman', sans-serif;
  font-size: 1rem;
  color: #000;
  line-height: 1;
  background: url("./images/background.jpg") center no-repeat;
  background-size: cover;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  cursor: pointer;
}

ul {
  padding: 0;
  margin: 0;
}

#root {
  min-height: 100vh;
}
